.card {
  display: inline-flex;
  justify-content: stretch;
  align-items: center;
  flex-direction: column;
  width: 400px;
  max-width: calc(100% - 20px - 20px);
  margin: 20px;
  background: var(--background);
  border-radius: var(--rounded);
  overflow: hidden;
  box-shadow: var(--shadow);
  vertical-align: top;
}

.card[data-style="project"] {
  width: 350px;
}

.card-image img {
  aspect-ratio: 16 / 9;
  object-fit: cover;
  width: 100%;
  // box-shadow: var(--shadow);
}

.card-text {
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  gap: 15px;
  max-width: 100%;
  padding: 20px;
}

.card-text > * {
  margin: 0 !important;
}

.card-title {
  font-family: var(--heading);
  font-weight: var(--semi-bold);
  font-size: larger;
}

.card-title[data-style="project"] {
  font-family: var(--heading);
  font-weight: var(--semi-bold);
  font-size: medium;
}

.card-subtitle {
  margin-top: -10px !important;
  font-style: italic;
}


.card-description {
  font-family: var(--body);
  line-height: normal;
  min-height: 150px;
}

.card-description[data-style="project"] {
  font-family: var(--body);
  line-height: normal;
  min-height: 180px;
}